<template>
  <div>
      <header>
        <img src="./assets/to-do-list.svg" width="60px" height="60px">
        <h1>TODO APP</h1>
      </header>

      <div class="sidebar">
            <div v-if="todoAdded">
              <h2>Zadania ({{counter}}) <span class="green">({{doneCounter}})</span></h2>
              <div v-for="todo in todos" :key="todo.name">
                <Todo :todo="todo"
                      @remove="removeTodo(todo)"
                      @setAtLastIndex="setAtLastIndex(todo)"
                      @increased="doneCounter++"></Todo>
              </div>
            </div>
            <div v-else>
              <h1 class="noTodos">Brak zadań :(</h1>
            </div>
      </div>

      <link href="https://fonts.googleapis.com/css?family=Roboto" rel="stylesheet">
      <div class="form__group">
        <input type="text" class="form__input" id="name" v-model="todo.name" placeholder="Nazwa Zadania" required/>

        <input type="text" class="form__input" id="name" v-model="todo.description" placeholder="Opis Zadania" required/>

        <input type="date" class="form__input" id="name" v-model="todo.deadline" required/>

        <button @click="addTask()" class="button button_primary">Dodaj zadanie</button>

      </div>

  </div>
</template>

<script>
import Todo from './components/Todo';

export default {
    components: {
      Todo
    },
    data(){
      return{
        todoAdded: false,
        counter: 0,
        doneCounter: 0,
        todos: [],
        todo: {
          name: '',
          description: '',
          deadline: '',
          done: false
        }
      }
    },

    methods: {
      addTask(){
        this.todos.unshift({
          name: this.todo.name, 
          description: this.todo.description, 
          deadline: this.todo.deadline,
          done: false
        })
        this.counter++;

        this.todoAdded = true
        this.todo.name = ''
        this.todo.description = ''
        this.todo.deadline = ''
      },

      removeTodo(todo){
        this.counter--;
        const todoIndex = this.todos.indexOf(todo)
        this.todos.splice(todoIndex, 1)
        
      },
      setAtLastIndex(todo){
        const todoIndex = this.todos.indexOf(todo)
        this.todos.splice(todoIndex, 1)
        this.todos.push(todo)
      }
    }
}
</script>


