<template>
    <div>
        <div :class="{todoDone: todo.done}" class="card bg-light mb-3 mt-4" style="max-width: 18rem; margin-left: 80px !important;">
            <div class="card-header">{{todo.deadline}}</div>
            <div class="card-body">
                <h5 class="card-title">{{todo.name}}</h5>
                <p class="card-text">{{todo.description}}.</p>
                <img @click="increaseDoneCounter(todo)" src="../assets/check.svg" width="30px" height="30px">
                <img @click="removeTodo(todo)" src="../assets/remove.svg" width="30px" height="30px">
            </div>
        </div>

        

    </div>
</template>

<script>
    export default {
        props: ['todo', 'doneCounter'],
        methods: {
            removeTodo(){
                this.$emit('remove')
            },
            increaseDoneCounter(){
                this.todo.done = !this.todo.done
                this.$emit('setAtLastIndex')
                this.$emit('increased')
            }
        }
    }
</script>

